import React from 'react';
import './stylesheet.css';
import { db, auth } from './firebase';
import { useState } from 'react';
import { useEffect } from 'react';
import Logo9 from './images/carousel/Evas Cantina & Grill logo.jpg'

const MenuItem = (props) => {
    const [state, setState] = useState({
        category: null,
    });

    const getCategory = async () => {
        console.log(props);
        console.log('mounted');
        const lengthOfCategory = await db
            .collection('restaurants')
            .doc(props.id.id)
            .collection('categories')
            .get();
        const category = [];
        const data = await db
            .collection('restaurants')
            .doc(props.id.id)
            .collection('categories')
            .doc(props.id.categoryid)
            .get();

        console.log(data.data());

        setState({ category: data.data() });
    };
    useEffect(() => {
        getCategory();
    }, [props]);

    return (
        <div class='menu-items-grid'>
            {state.category !== null ? (
                state.category.items ? (
                    state.category.items.map((item, index) => {
                        console.log(item);
                        return (
                            <a key={index}>
                                <div class='menu-item'>
                                    <button type="button" data-toggle="modal" data-target="#myModal"><img src={item.image_url} /></button>

                                    <div id="myModal" class="modal fade" tabindex="-1" role="dialog">
                                        <div class="modal-dialog">
                                            <div class="modal-content">
                                                <div class="modal-body">
                                                    <img src={item.image_url} />
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                    <div class='menu-item-info'>
                                        <div class='menu-item-top'>
                                            <h3>{item.name}</h3>
                                            <p>${item.price}</p>
                                        </div>
                                        <pre>{item.description}</pre>
                                    </div>
                                </div>
                            </a>
                        );
                    })
                ) : state.category.categories ? (
                    state.category.categories.map((cat, i) =>
                        cat.items
                            ? cat.items.map((val, l) => (
                                <a key={i}>
                                    <div class='menu-item'>

                                        <button type="button" data-toggle="modal" data-target="#myModal"><img src={val.image_url} /></button>

                                        <div id="myModal" class="modal fade" tabindex="-1" role="dialog">
                                            <div class="modal-dialog">
                                                <div class="modal-content">
                                                    <div class="modal-body">
                                                        <img src={val.image_url} />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                        <div class='menu-item-info'>
                                            <div class='menu-item-top'>
                                                <h3>{val.name}</h3>
                                                <p>${val.price}</p>
                                            </div>
                                            <pre>{val.description}</pre>
                                        </div>
                                    </div>
                                </a>
                            ))
                            : null
                    )
                ) : null
            ) : (
                    <div class="spinner-border text-danger" role="status">
                        <span class="sr-only">Loading...</span>
                    </div>
                )}
        </div>
    );
};

export default MenuItem;
