import React from 'react';
import './stylesheet.css';
import MenuItem from './menu-item'
import Nav from './components/navBar'
import Footer from './components/footer';


class SubMenu extends React.Component {

    render() {
        return (
            <div>
                <Nav />
                <div class="menu-items-title">
                    <div id="restaurant-category-bg">
                        <div class="menu-items-header" style={{ backgroundImage: `url(${this.props.location.state.image})` }}>
                            <h3>{this.props.location.state.name}</h3>
                        </div>
                    </div>

                    <div>
                        <MenuItem id={this.props.location.state.id} />
                    </div>
                </div>
                <Footer />
            </div>
        )
    }
}


export default SubMenu;